@charset "UTF-8";
:root {
  --font-family-base: "Noto Sans JP", sans-serif;
  --font-family-second: "Noto Sans", "Noto Sans JP", sans-serif;
  --font-family-third: "gill-sans-nova", "Noto Sans", "Noto Sans JP", sans-serif;
  --font-family-fourth: "dnp-shuei-gothic-kin-std", sans-serif;
}

:root {
  --font-weight-regular: 400;
  --font-weight-normal: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
}

:root {
  --black: #000;
  --white: #fff;
  --main-orange: #EE8A43;
  --main-blue: #036EB8;
  --main-yellow: #FCBF42;
  --main-pink: #F9B1BF;
  --main-beige: #DFDBBB;
  --main-green: #428B8D;
  --main-white: #F9F9F9;
  --bg-pink: rgba(249, 177, 191, 0.40);
  --bg-beige: rgba(223, 219, 187, 0.85);
  --bg-green: rgba(66, 139, 141, 0.75);
  --bg-orange: rgba(238, 138, 67, 0.20);
  --bg-blue: rgba(3, 110, 184, 0.20);
  --bg-gray: #EDEBDC;
}

:root {
  --z-index-header: 200;
  --z-index-to-top: 200;
  --z-index-drawer: 1000;
  --z-index-hamburger: 1001;
  --z-index-modal: 10000;
}

:root {
  --cq-ref-mobile: 390;
  --cq-ref: 1440;
  --fluid-basis-mobile: (100vw - 320px) / (390 - 320);
  --fluid-basis: (100vw - 768px) / (1440 - 768);
  --fluid-basis-2: (100vw - 1440px) / (1800 - 1440);
}

:root {
  --duration-short: 0.2s;
  --duration-base: 0.3s;
  --duration-long: 0.6s;
  --duration-fv: 0.7s;
  --ease-smooth-bounce: cubic-bezier(.48, .07, .33, .79);
}

body {
  color: var(--black);
  font-family: var(--font-family-base);
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 390px) {
  html {
    font-size: 4.1025641026vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.6vw;
  }
}
@media (min-width: 1000px) {
  html {
    font-size: 16px;
  }
}

body {
  margin-top: 4.6875rem;
}
@media screen and (min-width: 768px) {
  body {
    margin-top: 6.5625rem;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  overflow-wrap: anywhere;
  word-break: normal;
  line-break: strict;
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul,
ol,
button,
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd,
time,
small {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

a,
span,
button {
  display: inline-block;
}

small,
time,
figure {
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
  transition: 0.3s;
}

button {
  color: inherit;
}

@media (any-hover: hover) {
  a:hover {
    cursor: pointer;
  }
}
@media (min-width: 768px) {
  a[href*="tel:"] {
    pointer-events: none;
    cursor: default;
    text-decoration: none;
  }
}
a {
  -webkit-tap-highlight-color: transparent;
}

button {
  -webkit-tap-highlight-color: transparent;
}

